import React from "react"
import styled from "styled-components"
import HeaderLink from "./HeaderLink"
import Devices from "../BreakPoint/Devices"

import navHeader from "../../../contents/headerNav.yml"

const HeaderNavigation = styled.nav`
  display: none;
  @media ${Devices.tablet} {
    display: flex;
    flex-direction: row;
    align-items: center;
    overflow-x: auto;
    overflow-y: hidden;
    height: 100%;
  }
`
const HeaderNavigationWrapper = () => {
  return (
    <HeaderNavigation>
      {navHeader.items.map(link => (
        <HeaderLink
          key={link.title}
          isActive={window.location.pathname.includes(link.activeSelector)}
          title={link.title}
          to={link.to}
        />
      ))}
    </HeaderNavigation>
  )
}

export default HeaderNavigationWrapper
